  <p style="text-align:justify ">Една от най-честите грешки при работата ни с растенията е прилагането на механичния подход към тях. Чете човекът надписа на пакетчето със семената за раннозрял разсад – „35 дни от засяване до прибирането на реколтата“ и отбелязва в календара: ще го посадя на 1 май, а на 5-6 юни ще прибирам. Реколтата ще съхранявам… А растението не е кафеварка с програма и инструкция за ползване. Растението е доста по-фино нещо. Терминът „раннозреещ-къснозреещ“ е нещо относително и условно свързано с някакви конкретни дати. Кога цъфти липата? В края на май-юни пише в книгите. Но се случват години, когато тя цъфти и по-рано. Само относително може да се говори за времето на узряване и само в рамките на конкретния ботанически вид. Така че най-добре е да се сравнява раннозрелостта на един сорт домат с други сортове домати, а на репичките с репички. Логично е на високорастящите сортове домати плодовете да се появят по-късно от нискостъблените, а на дребните, ранни репички по-рано от едрите, къснозреещи. Как да разберем, обаче, кога ще цъфнат и узреят „раннозрелите“ и кога „къснозреещите“ сортове? Когато агрономите говорят за скорост на развитие на отделната култура, те въвеждат понятето сума на ефективните температури и се базират не на конкретни дати или дни, а точно на тази сума. Сега нека уточним за какво иде реч. За всяко растение и въобще жив организъм са важни два температурни параметъра.<br><br>

<b>1. Температурата, под чийто праг растението загива. Например – за далията критични са температурите под нулата, а за какаовото дърво – температурата под +14 градуса.</b><br><br>

<b>2. Температурата, при която растението започва да расте – т. нар. биологичен минимум. Той е специфичен за всяка една култура. При пшеницата, овеса, ечемика и граха това е +5 градуса. Тези култури наричаме студоустойчиви. А при памука биологическият минимум е +12 градуса. Като средна стойност за мнозинството култури, отглеждани в умерения пояс, биологичният минимум е равен на +10 градуса.</b><br><br>

Нека сега да разгледаме и самата сума на ефективните температури.  Тази година среднодневната температура в района на Велико Търново премина 10 градуса на 2-ри март. Отваряме градинарския си дневник и записваме: 2-ри март: +12.45, 3-ти март: +15.72. И така всеки ден отбелязваме среднодневните температури. Опа! 5-ти март: +8.1. Тази цифра няма да я запишем, нас ни интересуват само дните, в които среднодневната температура надвишава +10 градуса. И така всеки ден.<br><br>

Накрая събираме сумата от среднодневните температури на всички дни, когато тя е била над +10 градуса. Това именно е сумата на ефективните температури. Към определен ден тя ще стане 100 градуса, а към друг – 1000. В една година тя ще достигне 500 градуса по-бързо, а през друга по-бавно.

Точно от тази сума зависи, кога растението ще цъфне и кога ще даде плод. Например, за да узреят, ранните моркови се нуждаят от 1300 градуса, а късните – от 1800. Студоустойчивите ябълки узряват към 1800-2200 градуса, а крушите – към 2200, толкова е нужно и на гроздето, за да узрее.</p>
           
