import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import about from '@/components/about'
import contact from '@/components/contact'
import map from '@/components/map'



Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/about',
      name: 'about',
      component: about
    },
     {
      path: '/map',
      name: 'map',
      component: map
    },
    {
      path: '/contact',
      name: 'contact',
      component: contact
    }
  ]
})
 
