<template>
 <div class="about">
  <h1>Покритие</h1>
 <l-map style="height: 350px" :zoom="zoom" :center="center">
<l-tile-layer :url="url"></l-tile-layer>
<l-geo-json :geojson="geojson"></l-geo-json>
</l-map>
   <p style="text-align:center "><b>Карта на покритие на услугата</b></p>
 </div>
</template>
<script>
import {LMap, LTileLayer, LGeoJson} from 'vue2-leaflet';
import 'leaflet/dist/leaflet.css'


 export default{
  name:'map',
   components: {
    LMap,
    LTileLayer,
    LGeoJson
    
  },
  data (){
   return{
    title:'About',
     url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
      zoom: 11,
      center: [43.075672, 25.617151],
      geojson: null
   };
  },
    async created () {
    const response = await fetch('https://cors-anywhere.herokuapp.com/http://87.254.182.122/bufer.geojson');
    this.geojson = await response.json();
  }
 }
</script><style scoped>
 .row h3{
  cursor:pointer;
}
</style> 
 
